                                <!-- ÂºÇÂ∏∏Áä∂ÊÄÅÊèêÁ§∫ -->
                                <view v-if="record.IsAbnormity === 'Y'" class="abnormal-tag">
                                    <text class="abnormal-text">ÂºÇÂ∏∏</text>
                                </view>
