                            <!-- Â°´ÂÖÖÁ©∫ÁôΩÂç°ÁâáÔºåÁ°Æ‰øù4ÂÆ´Ê†ºÂ∏ÉÂ±Ä -->
                            <view v-for="n in (4 - page.length)" :key="'empty-' + pageIndex + '-' + n"
                                class="overview-card empty-card" @click="ToHealthIndicatorList()">
                                <view class="card-icon">
                                    <text class="default-icon">‚ûï</text>
                                </view>
                                <text class="card-label">Ê∑ªÂä†ËÆ∞ÂΩï</text>
                                <text class="card-desc">ÁÇπÂáªËÆ∞ÂΩïÂÅ•Â∫∑Êï∞ÊçÆ</text>
                            </view>
                        </view>
                    </swiper-item>

                    <!-- Ê≤°ÊúâÊï∞ÊçÆÊó∂ÊòæÁ§∫Âç†‰ΩçÈ°µÈù¢ -->
                    <swiper-item v-if="todayRecordList.length === 0" class="swiper-page">
                        <view class="overview-grid">
                            <view class="overview-card empty-card" v-for="n in 4" :key="'placeholder-' + n"
                                @click="ToHealthRecordList()">
                                <view class="card-icon">
                                    <text class="default-icon">üìä</text>
                                </view>
                                <text class="card-label">ÊöÇÊó†Êï∞ÊçÆ</text>
                                <text class="card-desc">ÁÇπÂáªËÆ∞ÂΩïÂÅ•Â∫∑Êï∞ÊçÆ</text>
                            </view>
                        </view>
                    </swiper-item>
                </swiper>
            </view>

            <!-- Âø´Êç∑ÂäüËÉΩ -->
            <view class="quick-actions">
                <view class="section-title">
                    <text class="title-text">Âø´Êç∑ÂäüËÉΩ</text>
                </view>
                <view class="action-grid">
                    <view class="action-item" @click="ToHealthRecordList()">
                        <view class="action-icon record">üìä</view>
                        <text class="action-label">ÂÅ•Â∫∑ËÆ∞ÂΩï</text>
                    </view>
                    <view class="action-item" @click="ToDietRecordList()">
                        <view class="action-icon diet">üçé</view>
                        <text class="action-label">È•ÆÈ£üËÆ∞ÂΩï</text>
                    </view>
                    <view class="action-item" @click="ToAIAnalyse()">
                        <view class="action-icon diet">ü§ñ</view>
                        <text class="action-label">AIÂàÜÊûê</text>
                    </view>
                    <view class="action-item" @click="ToSportRecord()">
                        <view class="action-icon sport">üèÉ‚Äç‚ôÇÔ∏è</view>
                        <text class="action-label">ËøêÂä®ËÆ∞ÂΩï</text>
                    </view>
                    <view class="action-item" @click="ToHealthView()">
                        <view class="action-icon sport">üìà</view>
                        <text class="action-label">ÂÅ•Â∫∑ËßÜÂõæ</text>
                    </view>
                    <view class="action-item"  @click="ToHealthNoticeList()">
                        <view class="action-icon reminderer">‚è∞ </view>
                          <text class="action-label">ÂÅ•Â∫∑ÊèêÈÜí</text>
                    </view>
                    <view class="action-item" @click="ToHealthArticleList()">
                        <view class="action-icon article">üìñ</view>
                        <text class="action-label">ÂÅ•Â∫∑Áü•ËØÜ</text>
                    </view>
                    <view class="action-item" @click="ToRecipeList()">
                        <view class="action-icon recipe">ü•ó</view>
                        <text class="action-label">ÂÅ•Â∫∑È£üË∞±</text>
                    </view>
                </view>
            </view>

            <!-- ÊúÄÊñ∞ÂÅ•Â∫∑ËµÑËÆØ -->
            <view class="health-news">
                <view class="section-title">
                    <text class="title-text">ÂÅ•Â∫∑ËµÑËÆØ</text>
                    <text class="more-text" @click="ToHealthArticleList()">Êü•ÁúãÊõ¥Â§ö ></text>
                </view>
                <view class="news-list">
                    <!-- ÊòæÁ§∫ÁúüÂÆûÁöÑÂÅ•Â∫∑ËµÑËÆØÊï∞ÊçÆ -->
                    <view class="news-item" v-for="article in healthArticleList" :key="article.Id"
                        @click="ToHealthArticleDetail(article.Id)">
                        <view class="news-content">
                            <text class="news-title">{{ article.Title }}</text>
                            <text class="news-desc">{{ getArticleDesc(article.Content) }}</text>
                            <view class="news-meta">
                                <text class="news-time">{{ formatTime(article.CreationTime) }}</text>
                                <text class="news-category">{{ article.HealthArticleTypeDto.Name }}</text>
                                <text class="news-views">{{ article.ViewCount }}ÈòÖËØª</text>
                            </view>
                        </view>
                        <view class="news-image">
                            <image v-if="article.Cover" :src="article.Cover" class="cover-image" mode="aspectFill" />
                            <text v-else class="placeholder-icon">üìñ</text>
                        </view>
                    </view>

                    <!-- Ê≤°ÊúâÊï∞ÊçÆÊó∂ÊòæÁ§∫Âç†‰ΩçÂÜÖÂÆπ -->
                    <view v-if="healthArticleList.length === 0" class="no-data">
                        <text class="no-data-text">ÊöÇÊó†ÂÅ•Â∫∑ËµÑËÆØ</text>
                    </view>
                </view>
            </view>
        </view>

        <!-- Â∫ïÈÉ®ÂØºËà™Ê†è -->
        <footer-bar />
    </view>
</template>

<script setup>
import { ref, computed } from 'vue';
import { useCommonStore } from "@/store";
import { Post } from "@/utils/http";
import { onHide, onLoad, onShow, onUnload } from "@dcloudio/uni-app";

// Ëé∑Âèñstore
const commonStore = useCommonStore();
const Token = computed(() => commonStore.Token)
const UserInfo = computed(() => commonStore.UserInfo)
const RoleType = computed(() => commonStore.RoleType)
const UserId = computed(() => commonStore.UserId)

const todayRecordList = ref([]);

// Â§ÑÁêÜÈáçÂ§çÊåáÊ†áÁöÑÂáΩÊï∞
const processDuplicateRecords = (records) => {
    if (!Array.isArray(records) || records.length === 0) {
        return records;
    }
    
    console.log('Â§ÑÁêÜÂâçÁöÑËÆ∞ÂΩï:', records);
    
    // ‰ΩøÁî®MapÊù•Â≠òÂÇ®ÊØè‰∏™ÊåáÊ†áÁöÑÊúÄÊñ∞ËÆ∞ÂΩï
    const recordMap = new Map();
    
    records.forEach(record => {
        const indicatorId = record.HealthIndicatorDto?.Id;
        if (!indicatorId) return;
        
        const existingRecord = recordMap.get(indicatorId);
        
        // Â¶ÇÊûúÊ≤°ÊúâÂ∑≤Â≠òÂú®ÁöÑËÆ∞ÂΩïÔºåÊàñËÄÖÂΩìÂâçËÆ∞ÂΩïÊõ¥Êñ∞ÔºåÂàôÊõ¥Êñ∞Map
        if (!existingRecord) {
            recordMap.set(indicatorId, record);
        } else {
            // ÊØîËæÉÊó∂Èó¥Ôºå‰øùÁïôÊúÄÊñ∞ÁöÑËÆ∞ÂΩï
            const currentTime = new Date(record.RecordTime || record.CreationTime || 0);
            const existingTime = new Date(existingRecord.RecordTime || existingRecord.CreationTime || 0);
            
            if (currentTime > existingTime) {
                recordMap.set(indicatorId, record);
            }
        }
    });
    
    const result = Array.from(recordMap.values());
    console.log('Â§ÑÁêÜÂêéÁöÑËÆ∞ÂΩï:', result);
    return result;
};

// ‰øÆÊîπÂêéÁöÑTodayRecordListApi
const TodayRecordListApi = async () => {
    try {
        let { Data } = await Post('/HealthIndicatorRecord/TodayRecordList', {
            RecordUserId: UserId.value
        });
        
        // Â§ÑÁêÜÈáçÂ§çÊåáÊ†á
        todayRecordList.value = processDuplicateRecords(Data || []);
    } catch (error) {
        console.error('Ëé∑Âèñ‰ªäÊó•ËÆ∞ÂΩïÂ§±Ë¥•:', error);
        todayRecordList.value = [];
    }
};

//ÂÅ•Â∫∑ÊèêÈÜí
const ToHealthNoticeList = () => {
    uni.navigateTo({
        url: '/pages/Front/HealthNoticeList'
    });
};


// ÂÅ•Â∫∑ËµÑËÆØÊï∞ÊçÆÁä∂ÊÄÅ
const healthArticleList = ref([]);

// ÂÅ•Â∫∑Êï∞ÊçÆÂàÜÈ°µÂ§ÑÁêÜ
const healthPages = computed(() => {
    if (todayRecordList.value.length === 0) return [];

    const pages = [];
    const pageSize = 4; // ÊØèÈ°µ4‰∏™Âç°Áâá

    for (let i = 0; i < todayRecordList.value.length; i += pageSize) {
        pages.push(todayRecordList.value.slice(i, i + pageSize));
    }

    return pages;
});

// ÂÖ∂‰ªñÂéüÊúâÂáΩÊï∞‰øùÊåÅ‰∏çÂèò...
const ToDietRecordList = () => {
    uni.navigateTo({
        url: '/pages/Front/DietRecordList'
    });
};
const ToSportRecord = () => {
    uni.navigateTo({
        url: '/pages/Front/SportRecordList'
    });
};

// ÂÅ•Â∫∑ËßÜÂõæToHealthView
const ToHealthView = () => {
    uni.navigateTo({
        url: '/pages/Front/HealthView'
    });
};
//AIÂàÜÊûê
const ToAIAnalyse = () => {
    uni.navigateTo({
        url: '/pages/Front/AiAnalyse'
    });
};

const ToHealthRecordList = () => {
    uni.navigateTo({
        url: '/pages/Front/HealthIndicatorRecordList'
    });
};
const ToHealthIndicatorList = () => {
    uni.navigateTo({
        url: '/pages/Front/HealthIndicatorList'
    });
};

const ToHealthArticleList = () => {
    uni.navigateTo({
        url: '/pages/Front/HealthArticleList'
    });
};

// Ë∑≥ËΩ¨Âà∞ÂÅ•Â∫∑ÊñáÁ´†ËØ¶ÊÉÖ
const ToHealthArticleDetail = (articleId) => {
    uni.navigateTo({
        url: `/pages/Front/HealthArticleDetail?HealthArticleId=${articleId}`
    });
};

const ToRecipeList = () => {
    uni.navigateTo({
        url: '/pages/Front/RecipeList'
    });
};

// ÊèêÂèñÊñáÁ´†ÊèèËø∞ÔºàÂéªÈô§HTMLÊ†áÁ≠æÔºåÊà™ÂèñÂâç50‰∏™Â≠óÁ¨¶Ôºâ
const getArticleDesc = (content) => {
    if (!content) return 'ÊöÇÊó†ÊèèËø∞';
    // ÂéªÈô§HTMLÊ†áÁ≠æ
    const plainText = content.replace(/<[^>]*>/g, '');
    // Êà™ÂèñÂâç50‰∏™Â≠óÁ¨¶
    return plainText.length > 50 ? plainText.substring(0, 50) + '...' : plainText;
};

// Ê†ºÂºèÂåñÊó∂Èó¥ÊòæÁ§∫
const formatTime = (timeStr) => {
    if (!timeStr) return '';

    const now = new Date();
    const articleTime = new Date(timeStr);
    const diff = now - articleTime;

    const minutes = Math.floor(diff / (1000 * 60));
    const hours = Math.floor(diff / (1000 * 60 * 60));
    const days = Math.floor(diff / (1000 * 60 * 60 * 24));

    if (minutes < 60) {
        return minutes <= 0 ? 'ÂàöÂàö' : `${minutes}ÂàÜÈíüÂâç`;
    } else if (hours < 24) {
        return `${hours}Â∞èÊó∂Ââç`;
    } else if (days < 7) {
        return `${days}Â§©Ââç`;
    } else {
        // Ë∂ÖËøá7Â§©ÊòæÁ§∫ÂÖ∑‰ΩìÊó•Êúü
        return articleTime.toLocaleDateString('zh-CN', {
            month: '2-digit',
            day: '2-digit'
        });
    }
};

// Ê†ºÂºèÂåñÂÅ•Â∫∑ËÆ∞ÂΩïÊï∞ÂÄºÊòæÁ§∫
const formatRecordValue = (value) => {
    if (value === null || value === undefined) return '--';
    
    // Â§ÑÁêÜË°ÄÂéãÁ≠âÁâπÊÆäÊ†ºÂºè
    if (typeof value === 'object') {
        if (value.Systolic && value.Diastolic) {
            return `${value.Systolic}/${value.Diastolic}`;
        }
        return JSON.stringify(value);
    }
    
    // Â¶ÇÊûúÊòØÊï¥Êï∞ÔºåÁõ¥Êé•ÊòæÁ§∫ÔºõÂ¶ÇÊûúÊòØÂ∞èÊï∞Ôºå‰øùÁïô1‰ΩçÂ∞èÊï∞
    const numValue = Number(value);
    return isNaN(numValue) ? value.toString() : 
           (numValue % 1 === 0 ? numValue.toString() : numValue.toFixed(1));
};

// Ê†πÊçÆÂÅ•Â∫∑ÊåáÊ†áÂêçÁß∞Ëé∑ÂèñÂçï‰Ωç
const getHealthIndicatorUnit = (name) => {
    const unitMap = {
        '‰ΩìÊ∏©': '‚ÑÉ',
        '‰ΩìÈáç': 'kg',
        'Ë∫´È´ò': 'cm',
        'ÂøÉÁéá': 'Ê¨°/ÂàÜ',
        'Ë°ÄÂéã': 'mmHg',
        'Á©∫ËÖπË°ÄÁ≥ñ': 'mmol/L',
        'È§êÂêé2Â∞èÊó∂Ë°ÄÁ≥ñ': 'mmol/L',
        'Ë°ÄÁ≥ñ': 'mmol/L',
        'ËÉÜÂõ∫ÈÜá': 'mmol/L',
        'Ë°ÄÁ∫¢ËõãÁôΩ': 'g/L',
        'ÁôΩÁªÜËÉû': '√ó10‚Åπ/L',
        'Ë°ÄÂ∞èÊùø': '√ó10‚Åπ/L'
    };

    // Ê®°Á≥äÂåπÈÖçÔºåÂåÖÂê´ÂÖ≥ÈîÆÂ≠óÂç≥ÂèØ
    for (const [key, unit] of Object.entries(unitMap)) {
        if (name && name.includes(key)) {
            return unit;
        }
    }

    return ''; // ÈªòËÆ§Êó†Âçï‰Ωç
};

// Ëé∑ÂèñÂÅ•Â∫∑Áü•ËØÜÂàóË°®
const GetHealthArticleListApi = async () => {
    try {
        const response = await Post('/HealthArticle/List', {
            Page: 1,
            Limit: 6,
            AuditStatus: 2,
            SortItem: {
                FieldName: "ViewCount",
                IsAsc: false
            }
        });
        if (response.Success && response.Data && response.Data.Items) {
            healthArticleList.value = response.Data.Items;
        }
    } catch (error) {
        console.error('Ëé∑ÂèñÂÅ•Â∫∑Áü•ËØÜÂàóË°®Â§±Ë¥•:', error);
        uni.showToast({
            title: 'Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•',
            icon: 'error'
        });
    }
};

onLoad(() => {
    // È°µÈù¢Âä†ËΩΩÊó∂ÁöÑÂàùÂßãÂåñÈÄªËæë
});

onShow(() => {
    if (commonStore.CheckIsLogin()) {
        GetHealthArticleListApi();
        TodayRecordListApi();
    }
});

onHide(() => {
    // È°µÈù¢ÈöêËóèÊó∂ÁöÑÈÄªËæë
});

onUnload(() => {
    // È°µÈù¢Âç∏ËΩΩÊó∂ÁöÑÈÄªËæë
});
</script>

